import React from "react";
import { slide as Menu } from "react-burger-menu";
import salad from './Assets/salad.jpg';
export default props => {
  return (
    // Pass on our props
    <Menu {...props}>
      <a className="menu-item"  href="/" img src={salad} alt="salad" >
        Home
      </a >

      <a className="menu-item" href="./View/Collaborator/starters">
        Starters
      </a>

      <a className="menu-item" href="./View/Collaborator/dishes">
        Dishes
      </a>

      <a className="menu-item" href="./View/Collaborator/desserts">
        Desserts
      </a>

      <a className="menu-item" href="./View/Collaborator/drinks">
        Drinks
      </a>

      <a className="menu-item" href="./View/Collaborator/account">
        Account
      </a>

      <a className="menu-item" href="./View/Collaborator/disconect">
        Disconect
      </a>
    </Menu>
  );
};
